.mt-8rem{
  margin-top: 8rem;
}

.cards {
  background: #fff;
}

h1 {
  text-align: center;
}

.cards__container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  align-items: center;
  /* max-width: 1120px;
  width: 90%; */
  margin: 0 auto;
}

.cardFilter{
display: flex;
gap: 2px;
}

.cardFilter button{
  padding: 10px;
}


.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.card-item {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

.card-img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  text-align: center;
  padding: 10px 0;
}

.overlay-text {
  font-size: 1.5rem;
  font-weight: bold;
}


.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: grid;
  margin: 0.5rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  height: 280px;
  padding-top: 67%;
  overflow: hidden;
  border-top-left-radius: 1.25rem;
  border-top-right-radius: 1.25rem;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  top: 20px;
  right: 0px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 500;
  color: #fff;
  background-color: #031244;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 10px 20px;
}

.cards__item__text {
  color: #000;
  font-size: 1rem;
  line-height: 24px;
}

.cards-item-price {
  color: #000;
  font-size: 1rem;
  line-height: 24px;
}

.cards-item-place {
  color: #000;
  font-size: 1.125rem;
  
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

.grid-columns{
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 5px;

}


@media only screen and (max-width: 1536px) {
  .grid-columns {
    grid-template-columns: repeat(4, minmax(0, 1fr)) !important;
}
}

@media only screen and (max-width: 1280px) {
  .grid-columns {
    grid-template-columns: repeat(3, minmax(0, 1fr)) !important;
}
}

@media only screen and (max-width: 992px) {
  .grid-columns {
    grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
}

}@media only screen and (max-width: 768px) {
  .grid-columns {
    grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
}
}

@media only screen and (max-width: 576px) {
  .grid-columns {
    grid-template-columns: repeat(1, minmax(0, 1fr))!important;
}
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}
