.egns-preloader {
    position: fixed !important;
    z-index: 99999;
    overflow: hidden
}

.egns-preloader,
.egns-preloader:before {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.egns-preloader:before {
    content: "";
    position: absolute;
    background: #fff;
    z-index: -1
}

.egns-preloader .circle-border {
    width: 100%;
    max-width: 140px;
    height: 100%;
    max-height: 140px;
    border: 0 solid var(--primary-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: auto;
    border-radius: 50%;
    box-shadow: 2px 2px 30px rgba(0, 0, 0, .1)
}

.egns-preloader .circle-border svg {
    fill: var(--primary-color1)
}

.egns-preloader .circle-border svg path {
    stroke: var(--primary-color1)
}

.egns-preloader .circle-border .moving-circle {
    animation: circle 2s linear infinite
}

.egns-preloader .circle-border .moving-circle,
.egns-preloader .circle-border .moving-circle:nth-child(2) {
    width: 10px;
    height: 10px;
    background: var(--primary-color1);
    border-radius: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    margin: auto
}

.egns-preloader .circle-border .moving-circle:nth-child(2) {
    animation: circle 2.25s linear infinite
}

.egns-preloader .circle-border .moving-circle:nth-child(3) {
    width: 10px;
    height: 10px;
    background: var(--primary-color1);
    border-radius: 50%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    margin: auto;
    animation: circle 2.5s linear infinite
}

@keyframes circle {
    0% {
        transform: rotate(0deg) translate(-70px) rotate(0deg)
    }

    to {
        transform: rotate(1turn) translate(-70px) rotate(-1turn)
    }
}

.egns-preloader.close {
    display: none;
    visibility: hidden
}

.preloader-close-btn {
    position: absolute;
    right: 20px;
    top: 30px;
    font-size: 16px;
    transition: .5s ease-in-out linear;
    text-transform: uppercase;
    font-weight: 400;
    cursor: pointer
}

