.product-card .for-border {
    width: 0
}

.product-card .for-border:before {
    content: "";
    position: absolute;
    right: -.5px;
    top: 0;
    height: 0%;
    width: .5px;
    background: rgba(0, 0, 0, .6);
    transition: .3s;
    transition-delay: .5s
}

.product-card .for-border:after {
    content: "";
    position: absolute;
    left: -.5px;
    bottom: 0;
    height: 0%;
    width: .5px;
    background: #000;
    transition: .3s;
    transition-delay: .6s
}

.product-card:before {
    left: 0;
    top: -.5px
}

.product-card:after,
.product-card:before {
    content: "";
    position: absolute;
    width: 0;
    height: .5px;
    background: #000;
    transition: .3s;
    transition-delay: .9s
}

.product-card:after {
    right: 0;
    bottom: -.5px
